import { Component, OnInit } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-template-solicitudes',
  templateUrl: './template-solicitudes.component.html',
  styleUrls: ['./template-solicitudes.component.css']
})
export class TemplateSolicitudesComponent implements OnInit {

  public formularioSolicitudes = this._formSolicitud.group({
    folio: new FormControl('', Validators.required),
    alias: new FormControl(),
    forma: new FormControl('', Validators.required),
    estatus: new FormControl('', Validators.required),
    version: new FormControl('', Validators.required),
    asignado: new FormControl(),
    comentarios: new FormControl(),
  });
  
  constructor(private _formSolicitud: FormBuilder) { }

  ngOnInit(): void {
  }

  public getFormControl(controlName: string): AbstractControl {
    return this.formularioSolicitudes.controls[controlName];
  }
  
  
}
