import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuardGuard } from 'src/app/utilities/auth-guard.guard';
import { RoleBaseAuthorization } from '../../models/login';
import { SolicitudesComponent } from './solicitudes/solicitudes.component';
import { TemplateSolicitudesComponent } from './template-solicitudes/template-solicitudes.component';

const routes: Routes = [
  {
    path: '', redirectTo:'solicitudes', pathMatch: 'full'
  },{
    path: 'solicitudes', 
    component: SolicitudesComponent,
    canActivate:[AuthGuardGuard],
    data:{ 
      breadcrumb:'Solicitudes', 
      roles:[ 
        RoleBaseAuthorization.ADMIN,
        RoleBaseAuthorization.COORDINADOR
      ]
    }
  },{
    path: 'template-solicitudes', 
    component: TemplateSolicitudesComponent,
    canActivate:[AuthGuardGuard],
    data:{ 
      breadcrumb:'Crear solicitud', 
      roles:[ 
        RoleBaseAuthorization.ADMIN,
        RoleBaseAuthorization.COORDINADOR
      ]
    }
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProyectosRoutingModule { }